.Modal {
    position: fixed;
    z-index: 500;
    background-color: rgb(72, 107, 172);
    width: 70%;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 15%;
    top: 5%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    max-height: 90%;
}

.ModalWide {
    position: fixed;
    z-index: 500;
    background-color: rgb(72, 107, 172);
    width: 90%;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 5%;
    top: 5%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    overflow: auto;
    max-height: 90%;
}

@media (min-width: 900px) {
    .Modal {
        top: 30%;
        width: 800px;
        left: calc(50% - 400px);
    }
}



